<script>
  export let classes;
  export let img;
</script>

<style>
  .post-thumb.-object-fit img,
  .post-thumb.-overlay {
    width: 100%;
    height: 100%;
  }
  .post-thumb.-object-fit img {
    object-fit: cover;
  }
  .post-thumb.-overlay {
    position: absolute;
    z-index: -2;

    background-color: #333;
  }
</style>

<div class="post-thumb {classes}">
  <picture>
    <img
      src="{img}"
      alt="" />
  </picture>
</div>
